@page {
  margin: 0.5cm; }

#uberform, #mainnav__nav, #preview, #stickybar, .emoji-bar, #tree, .actionnav {
  display: none; }

#mainnav .version img {
  display: inline-block; }

textarea {
  overflow: visible;
  width: 100%; }

label {
  display: block; }

input[type="checkbox"] + label {
  display: inline-block; }

details {
  display: none; }
  details[open] {
    display: block; }
  details > * {
    display: block !important; }

summary h1, summary h2, summary h3, summary h4, summary h5, summary h6 {
  display: inline-block; }

p a {
  word-wrap: break-word; }
